.home{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    // margin: 1rem 0rem;
}

.bg-img-home{
    position: relative;
    width: 100%;
    height: 40vw;
    // background-color: red;
}

.home-bg-img-cont{
    width: 100%;
    height: 40vw;
    background-color: pink;
    position: absolute;
    top: 0;
    left: 0;
}

.home-bg-img{
    width: 100%;
    object-fit: cover;
    animation-name: fade;
    animation-duration: 1.5s;
}
@keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }

.img-home1-content{
    // position: absolute;
}

.slide-dots{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 0;
}

.slide-dot-container{
    cursor: pointer;
    height: 1.5rem;
    width: 1.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: gray;
}

.slide-dot-inner{
    height: 1rem;
    width: 1rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(199, 104, 104);
}

.slide-dot-inner-inactive{

}

.a{
    height: 20rem;
    width: 100%;
    background-color: yellow;
    position: relative;
}
.b{
    height: 10%;
    background-color: red;
    
}
.c{
    height: 90%;
    background-color: aqua;
    position: absolute;
    top: 0;
}
