footer{
    // background-color: pink;
    display: flex;
    flex-direction: column;
    // gap: 0.1rem;
    font-weight: bold;
}

.footer-top{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    background-color: #451701;
    color: #ebaf37;
    padding-bottom: 1rem;
}

.footer-top1{
    margin-top: 1rem;
}

.footer-top2{
    display: flex;
    gap: 1rem;
}

.footer-top2-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 0.1rem solid goldenrod;
    border-radius: 50%;
    padding: 3rem;
    height: 0.5rem;
    width: 0.5rem;
}

.footer-middle1{
    color: white;
    height: 12rem;
    // background-color: yellow;
    background-color: black;
    display: flex;
    width:  100%;
    align-items: center;
    justify-content: center;
}
.footer-middle1-cont{
    width: 95%;
    // background-color: lime;
    display: flex;
    align-items: center;
    height: 100%;

    gap: 1rem;
}
.footer-middle1-i{
    flex: 1;
    // background-color: aqua;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.footer-middle1-i1{
    display: flex;
    // display: none;
    flex-direction: column;
    background-color: red;
    justify-content: center;
    align-items: center;
    // padding: 0.3rem ;
    width: 80%;
    // width: 100%;
    height: 40%;
    // height: 100%;
    // height: 1rem;
    border: 1px solid white;
    border-radius: 0.5rem;
}
.footer-middle1-i2{
    display: flex;
    // display: none;
    flex-direction: column;
    background-color: green;
    justify-content: center;
    align-items: center;
    // padding: 0.3rem ;
    width: 80%;
    // width: 100%;
    height: 40%;
    // height: 100%;
    // height: 1rem;
    border: 1px solid white;
    border-radius: 0.5rem;
}

.footer-middle1-i.fm1i-4{
    display: flex;
    flex-direction: column;
    font-weight: bold;
    gap: 0.5rem;
    color: rgb(5, 189, 5);
}

.mapouter{
    // overflow: hidden;
    position:relative;
    // background-color: black;
    // text-align:right;
    // height:200px;
    // width:200px;
    margin: 0;
    // background-color: yellow;
    width: 100%;
    height: 70%;
    display: flex;
    flex: 1;
    border-radius: 1rem;
    overflow: hidden;
}

.gmap_canvas{
    overflow:hidden;
    // background:none!important;
    // height:369px;
    // width:409px;
    margin: 0;
    width: 100%;
    height: 100%;
    // background-color: aqua;
    flex: 1;
    position: relative;
}

.addr-if{
    margin: 0;
    // display: none;
    // background-color: red;
    width: 100%;
    height: 100%;
    // overflow: hidden;
    // border-radius: 1rem;
}
.footer-middle1-contact-cont{
    font-size: 0.8rem;
    text-align: center;
}

.footer-middle2{
    width: 100%;
    background-color: black;
}

.footer-middle2-cont{
    // background-color: aqua;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}
.icons-in-contact{
    display: flex;
    gap: 0.5rem;
}

.footer-bottom{
    width: 100%;
    background-color: gold;
    text-align: center;
    padding: 0.2rem 0;
    font-weight: bold;
    font-size: 0.8rem;
    // margin-top: 1rem;
}

